<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link
            class="breadcrumbs__link"
            :to="{
              name: 'main',
            }"
          >
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link
            class="breadcrumbs__link"
            :to="{
              name: 'cart',
            }"
          >
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> 3 товара </span>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="order"
      >
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              v-model="formData.name"
              :error="formError.name"
              title="ФИО"
              placeholder="Введите ваше полное имя"
            />

            <BaseFormText
              v-model="formData.address"
              :error="formError.address"
              title="Адрес доставки"
              placeholder="Введите ваш адрес"
            />

            <BaseFormText
              v-model="formData.phone"
              :error="formError.phone"
              title="Телефон"
              placeholder="Введите ваш телефон"
            />

            <BaseFormText
              v-model="formData.email"
              :error="formError.email"
              title="Email"
              placeholder="Введи ваш Email"
            />

            <BaseFormTextarea
              v-model="formData.comment"
              :error="formError.comment"
              title="Комментарий к заказу"
              placeholder="Ваши пожелания"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="500"
                  />
                  <span class="options__value">
                    Курьером <b>{{ deliveryPrice | numberFormat }} ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="card"
                  />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="cash"
                  />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <CartOrders
          :products="products()"
          :totalAmount="totalAmount()"
          :totalPrice="totalPrice()"
        >
          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </CartOrders>

        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import axios from "axios";
import BaseFormField from "@/components/BaseFormField";
import BaseFormText from "@/components/BaseFormText";
import BaseFormTextarea from "@/components/BaseFormTextarea";
import CartOrders from "@/components/CartOrders";
import numberFormat from "@/helpers/numberFormat";
import { API_BASE_URL } from "@/config.js";

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: "",
      deliveryPrice: 500,
    };
  },
  components: {
    BaseFormField,
    BaseFormText,
    BaseFormTextarea,
    CartOrders,
  },
  filters: {
    numberFormat,
  },
  methods: {
    ...mapGetters({
      products: "cartDetail",
      totalPrice: "cartTotalPrice",
      totalAmount: "cartTotalNumber",
    }),
    ...mapMutations(["updateOrderInfo"]),
    order() {
      this.formError = {};
      this.formErrorMessage = "";
      axios({
        method: "POST",
        url: `${API_BASE_URL}/api/orders`,
        params: {
          userAccessKey: this.$store.state.userAccessKey,
        },
        data: this.formData,
      })
        .then((response) => {
          this.$store.commit("resetCart");
          this.formData = {};
          this.updateOrderInfo(response.data);
          this.$router.push({
            name: "orderInfo",
            params: {
              id: response.data.id,
            },
          });
        })
        .catch((e) => {
          this.formError = e.response.data.error.request || {};
          this.formErrorMessage = e.response.data.error.message;
        });
    },
  },
};
</script>